.single-event__title {
    padding: 0 0.5em;
    font-size: 1.5rem;
    text-align: center;
}

.single-event__infobar {
    margin: 0.5em;
    @include boxShadow($silver);
    padding: 1em 0.5em;
    @include curveAllCorners;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-row-gap: 2em;
    justify-content: center;
    align-items: center;
}

.single-event__infobar span,
.single-event__infobar span a {
    display: flex;
}

.single-event__content {
    padding: 0.5em;
}

.single-event__description {
    margin: 0.5em 0;
}

.single-event__embed {
    width: 100%;
    min-height: 13em;
}

